exports.push([module.id, "\n.promo-progress {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -webkit-box-align: center;\n      -ms-flex-align: center;\n          align-items: center;\n  margin-top: 8px;\n}\n.promo-progress__progress {\n  width: 100%;\n  margin-left: 15px;\n}\n.promo-progress .progress {\n  margin-bottom: 0;\n}\n", "", {"version":3,"sources":["/Users/Urij/code/mossebo-shop-admin/resources/assets/js/components/shop/promoCodes/resources/assets/js/components/shop/promoCodes/PromoCodesTable.vue"],"names":[],"mappings":";AA8cA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,0BAAA;MAAA,uBAAA;UAAA,oBAAA;EACA,gBAAA;CACA;AAEA;EACA,YAAA;EACA,kBAAA;CACA;AAEA;EACA,iBAAA;CACA","file":"PromoCodesTable.vue","sourcesContent":["<script>\n  import bTable from 'bootstrap-vue/es/components/table/table'\n  import bPagination from 'bootstrap-vue/es/components/pagination/pagination'\n  import bFormSelect from 'bootstrap-vue/es/components/form-select/form-select'\n  import bModal from 'bootstrap-vue/es/components/modal/modal'\n\n  import Core from '../../../core'\n\n  import Toggle from '../../Toggle'\n  import Loading from '../../Loading'\n\n  import TablePage from '../../../mixins/TablePage'\n  import Translatable from '../../../mixins/Translatable'\n  import StatusChangeable from '../../../mixins/StatusChangeable'\n  import PromoCodesTableModel from '../../../resources/shop/promo/PromoCodesTableModel'\n\n  const defaultTableState = {\n    page: 1,\n    perPage: 30,\n    type: 'all'\n  }\n\n\n  export default {\n    name: 'promo-codes-table',\n\n    mixins: [\n      StatusChangeable,\n      TablePage,\n      Translatable,\n    ],\n\n    components : {\n      Toggle,\n      Loading,\n      bTable,\n      bPagination,\n      bFormSelect,\n      bModal,\n    },\n\n    data () {\n      let fields = [\n        {\n          key: 'id',\n          sortable: false,\n        },\n        {\n          key: 'name',\n          sortable: false,\n          label: 'Код',\n        },\n        {\n          key: 'discount',\n          sortable: false,\n          label: 'Скидка',\n          thStyle: {\n            width: \"40%\"\n          }\n        },\n      ]\n\n      if (this.userCan('see')) {\n        fields.push({\n          key: 'quantity',\n          sortable: false,\n          label: 'Использований',\n          thStyle: {\n            width: \"40%\"\n          }\n        })\n      }\n\n      if (this.userCan('see')) {\n        fields.push({\n          key: 'conditions',\n          label: 'Условия',\n          sortable: false,\n          thStyle: {\n            width: \"30%\"\n          }\n        })\n      }\n\n      if (this.userCan('edit')) {\n        fields.push({\n          key: 'enabled',\n          sortable: false,\n        })\n      }\n\n      if (this.userCan('delete')) {\n        fields.push({\n          key: 'delete',\n          sortable: false,\n        })\n      }\n\n      return {\n        rbacNamespace: 'promo-codes',\n        loading: false,\n        totalRows: 0,\n        perPageOptions: [ 15, 30, 60 ],\n        types: {\n          all: 'Все',\n          enabled: 'Активные',\n          old: 'Завершенные',\n          disabled: 'Отмененные',\n        },\n        ... defaultTableState,\n        fields,\n\n        currencies: null,\n\n        usedMainData: [\n          'currencies',\n        ],\n\n        countByType: {\n          unconfirmed: 0,\n          deleted: 0,\n        }\n      }\n    },\n\n    created() {\n      let query = window.location.search.replace('?', '')\n\n      if (_.isEmpty(query)) return\n\n      query.split('&').forEach(item => {\n        item = item.split('=')\n\n        let key = item[0]\n        let value = item[1]\n\n        this[key] = this.getValid(key, value)\n      })\n    },\n\n    methods: {\n      loadData() {\n          this.fetchMainData()\n            .then(this.initMainData)\n      },\n\n      getValidPage(value) {\n        value = parseInt(value)\n        return isNaN(value) ? defaultTableState.page : value\n      },\n\n      getValidType(value) {\n        return value in this.types ? value : defaultTableState.type\n      },\n\n      getValidPerPage(value) {\n        value = parseInt(value)\n\n        if (this.perPageOptions.indexOf(value) !== -1) {\n          return value\n        }\n\n        return this.perPageOptions[0]\n      },\n\n      getValid(key, value) {\n        let methodName = 'getValid' + Core.camelize(key, true)\n\n        return this[methodName](value)\n      },\n\n      setHistoryState() {\n        let queryArr = Object.keys(defaultTableState).reduce((acc, key) => {\n          let validValue = this.getValid(key, this[key])\n\n          if (validValue !== defaultTableState[key]) {\n            acc.push(encodeURIComponent(key) + '=' + encodeURIComponent(this[key]))\n          }\n\n          return acc\n        }, [])\n\n        let pathWithQuery = window.location.pathname\n\n        if (queryArr.length > 0) {\n          pathWithQuery += '?' + queryArr.join('&')\n        }\n\n        if (this.$router.path !== pathWithQuery) {\n          this.$router.push(pathWithQuery)\n        }\n      },\n\n      /*\n        Загрузка списка.\n      */\n\n      fetchItems ({currentPage, perPage}) {\n        return new Promise(resolve => {\n          new Core.requestHandler('get', this.makePageApiUrl(), {\n            page: currentPage,\n            perPage,\n            type: this.type\n          })\n            .success(response => {\n              const data = response.data\n\n              this.totalRows = this.nanToNum(parseInt(data.totalRows))\n              this.page = parseInt(data.page) || 1\n              this.perPage = this.nanToNum(parseInt(data.perPage))\n\n              const items = data['promo-codes'] || []\n\n              resolve(items.map(item => new PromoCodesTableModel(item, this.currencies)))\n            })\n            .start()\n        })\n      },\n\n      setType(type) {\n        if (this.type === type) return\n\n        this.type = type\n\n        this.refreshTable()\n      },\n\n      sortingChanged(ctx) {\n        ctx.page = 1\n      },\n\n      setPerPage(value) {\n        this.perPage = value\n        this.page = 1\n      },\n\n      refreshTable() {\n        this.$nextTick(() => {\n          this.$refs.table.refresh()\n        })\n      },\n\n      nanToNum(value, num = 0) {\n        return isNaN(value) ? num : value\n      }\n    },\n\n    computed: {\n      showedFrom() {\n        return this.nanToNum((this.page - 1) * this.perPage + 1)\n      },\n\n      showedTo() {\n        let to = this.page * this.perPage\n        return this.nanToNum(to > this.totalRows ? this.totalRows : to)\n      },\n\n      showPagination() {\n        return this.perPage < this.totalRows;\n      },\n    },\n  }\n</script>\n\n<template>\n  <div>\n    <div class=\"block full\">\n\n      <div class=\"block-title clearfix\">\n        <h1>\n          <strong>\n            Промокоды\n          </strong>\n        </h1>\n\n        <div class=\"block-title-control\">\n          <router-link v-if=\"userCan('create')\" to=\"/shop/promo-codes/create\" class=\"btn btn-sm btn-success active\">\n            <i class=\"fa fa-plus-circle\"></i> Создать\n          </router-link>\n        </div>\n      </div>\n\n      <loading :loading=\"loading\">\n        <div class=\"table-responsive\" style=\"overflow: visible\">\n          <div class=\"dataTables_wrapper form-inline no-footer\">\n            <div class=\"row\">\n              <div class=\"col-sm-6 col-xs-12 clearfix\">\n                <div class=\"dataTables_paginate paging_bootstrap\" v-if=\"showPagination\">\n                  <b-pagination :total-rows=\"totalRows\" :per-page=\"perPage\" v-model=\"page\" class=\"my-0\" />\n                </div>\n              </div>\n\n              <div class=\"col-sm-6 col-xs-12 text-center clearfix\">\n                <div class=\"pull-right\">\n                  <div class=\"btn-group\">\n                    <template v-for=\"(title, typeIdentif) in types\">\n                      <button\n                        :class=\"{'btn btn-primary': true, 'active': type === typeIdentif}\"\n                        @click=\"setType(typeIdentif)\"\n                      >\n                        {{ title }}\n\n                        <template v-if=\"typeIdentif in countByType && countByType[typeIdentif] > 0\">\n                          <span class=\"badge\">{{ countByType[typeIdentif] }}</span>\n                        </template>\n\n                      </button>\n                    </template>\n                  </div>\n                </div>\n              </div>\n            </div>\n\n            <b-table\n              @refreshed=\"setHistoryState\"\n              show-empty\n              stacked=\"md\"\n              ref=\"table\"\n              :items=\"currencies ? fetchItems : null\"\n              :fields=\"fields\"\n              :busy.sync=\"loading\"\n              :current-page=\"page\"\n              :per-page=\"perPage\"\n              empty-text=\"Список промокодов пуст.\"\n              empty-filtered-text=\"Промокоды с такими параметрами не найдены.\"\n              style=\"margin-bottom: 0\"\n              class=\"table table-vcenter table-condensed table-hover table-bordered no-footer\">\n\n              <!--  ID  -->\n\n              <template slot=\"HEAD_id\" slot-scope=\"promoCode\">\n                <span class=\"table-column-id\">ID</span>\n              </template>\n\n              <template slot=\"id\" slot-scope=\"promoCode\">\n                <router-link :to=\"promoCode.item.url\">\n                  <span class=\"table-column-id\">\n                    {{ promoCode.item.id }}\n                  </span>\n                </router-link>\n              </template>\n\n              <!--  Код  -->\n\n              <template slot=\"name\" slot-scope=\"promoCode\">\n                <router-link :to=\"promoCode.item.url\">\n                  <span class=\"table-column-promocode\">\n                    {{ promoCode.item.name }}\n                  </span>\n                </router-link>\n              </template>\n\n              <!--  Количество  -->\n\n              <template slot=\"quantity\" slot-scope=\"promoCode\">\n                <template v-for=\"quantityString in promoCode.item.quantityStrings\">\n                  <div v-html=\"quantityString\"></div>\n                </template>\n\n                <template v-if=\"promoCode.item.uses_percent\">\n                  <div class=\"promo-progress\">\n                    <div class=\"promo-progress__num\">\n                      {{ promoCode.item.uses_count }}/{{ promoCode.item.quantity }}\n                    </div>\n\n                    <div class=\"promo-progress__progress\">\n                      <div class=\"progress\">\n                        <div class=\"progress-bar progress-bar-info\" role=\"progressbar\" :style=\"{width: promoCode.item.uses_percent + '%'}\">\n                          {{ promoCode.item.uses_percent }}%\n                        </div>\n                      </div>\n                    </div>\n                  </div>\n\n                </template>\n              </template>\n\n              <!--  Условия  -->\n\n              <template slot=\"conditions\" slot-scope=\"promoCode\">\n                <span class=\"table-column-conditions\">\n                  <template v-for=\"condition in promoCode.item.conditions\">\n                    <div v-html=\"condition.toString()\"></div>\n                  </template>\n                </span>\n              </template>\n\n              <!--  Статус  -->\n\n              <template slot=\"HEAD_enabled\" slot-scope=\"promoCode\">\n                <span class=\"table-column-enabled\">Статус</span>\n              </template>\n\n              <template slot=\"enabled\" slot-scope=\"promoCode\">\n                <span class=\"table-column-enabled\">\n                  <toggle @change=\"statusChange(promoCode.item.id)\" :checked=\"promoCode.item.enabled\" :key=\"promoCode.item.id\" />\n                </span>\n              </template>\n\n              <!--  Удаление  -->\n\n              <template slot=\"HEAD_delete\" slot-scope=\"promoCode\">\n                <span class=\"table-column-delete\"></span>\n              </template>\n\n              <template slot=\"delete\" slot-scope=\"promoCode\">\n                <span class=\"table-column-delete\">\n                  <a class=\"btn btn-danger\" @click=\"remove(promoCode.item.id)\">\n                    <i class=\"fa fa-times\"></i>\n                  </a>\n                </span>\n              </template>\n            </b-table>\n\n            <div class=\"row\">\n              <div class=\"col-sm-6 col-xs-12 clearfix\">\n                <div class=\"dataTables_paginate paging_bootstrap\" v-if=\"showPagination\">\n                  <b-pagination\n                    :total-rows=\"totalRows\"\n                    :per-page=\"perPage\"\n                    v-model=\"page\"\n                    class=\"my-0\"\n                  />\n                </div>\n              </div>\n\n              <div class=\"col-sm-6 col-xs-6\">\n                <div class=\"dataTables_length\">\n                  <label>\n                    <b-form-select :options=\"perPageOptions\" :value=\"perPage\" @change=\"setPerPage\" />\n                  </label>\n                </div>\n\n                <div class=\"dataTables_info\" role=\"status\" aria-live=\"polite\">\n                  <strong>{{ showedFrom }}</strong> - <strong>{{ showedTo }}</strong> из <strong>{{ totalRows }}</strong>\n                </div>\n              </div>\n            </div>\n          </div>\n\n        </div>\n      </loading>\n    </div>\n\n    <b-modal\n      id=\"removeModal\"\n      ref=\"removeModal\"\n      title=\"Удаление промокода\"\n      title-tag=\"h3\"\n      centered\n      ok-title=\"Удалить\"\n      cancel-title=\"Отмена\"\n      hide-header-close\n      @ok=\"removeConfirm\">\n\n      Вы действительно хотите удалить промокод?\n    </b-modal>\n  </div>\n</template>\n\n\n<style>\n  .promo-progress {\n    display: flex;\n    align-items: center;\n    margin-top: 8px;\n  }\n\n  .promo-progress__progress {\n    width: 100%;\n    margin-left: 15px;\n  }\n\n  .promo-progress .progress {\n    margin-bottom: 0;\n  }\n</style>"],"sourceRoot":""}]);

